.news,
.hits {
  background-color: #fff;
  padding-top: 20px;
  padding-bottom: 20px;
  h1 {
    margin-left: 30px; }
  &-slider {
    margin-top: 25px;
    margin-left: 30px;
    margin-right: 30px;
    &__in {
      width: 180px !important;
      position: relative;
      height: 250px;
      & + div {
        margin-left: 20px; } }
    &__img {
      max-width: 100%;
      height: 135px;
      margin-bottom: 5px; }
    &__link {
      color: #000000;
      font-size: 18px;
      font-weight: 400; }
    &__price {
      color: #444444;
      font-size: 22px;
      font-weight: 400;
      position: absolute;
      right: 18px;
      bottom: 0; } } }



