@include font('pfdin-regular', 'pfdintextcondpro-regular');
@include font('pfdin-italic', 'pfdintextcondpro-italic');
@include font('tt0248', 'tt0248m');
@include font('tt0249', 'tt0249m');

body {
    background-color: #eee;
    min-width: 1254px;
    font-family: 'pfdin-regular', sans-serif; }

.container {
    width: 1254px;
    margin: auto; }

.clr {
  @include clr; }

a {
  text-decoration: none;
  &:hover {
    text-decoration: underline; }
  &:active {
    text-decoration: underline; } }

.section {
  & + .section {
    margin-top: 40px; }
  &__title {
    color: #444444;
    font-family: "PF Din Text Cond Pro";
    font-size: 30px;
    font-weight: 400;
    line-height: 40px;
    text-transform: uppercase; }
  &__hr {
    border-top: 0;
    border-bottom: 1px solid #eee; } }
